<template>
  <div class="main-page">
    <h2 class="title">Конвертер валюты</h2>
    <div class="from">
      <div class="input-field from-currency">
        <p for="from-currency">
          Выберите валюту, которую хотите сконвертировать
        </p>
        <select
          name="from-currency"
          id=""
          class="browser-default"
          v-model="fromCurrency"
        >
          <option
            :value="currency[0]"
            v-for="(currency, index) in sortedCurrencies"
            :key="index"
            >{{ currency[0] + "-" + currency[1] }}</option
          >
        </select>
      </div>
      <div class="input-field from-currency-input">
        <input
          type="text"
          class="amount"
          name="amount"
          v-model="amountFromCurrency"
          placeholder="Количество валюты"
        />
      </div>
    </div>
    <div class="to">
      <div class="input-field to-currency">
        <p>конвертировать в:</p>
        <select
          name="currencies"
          id=""
          class="browser-default"
          v-model="toCurrency"
        >
          <option
            :value="currency[0]"
            v-for="(currency, index) in sortedCurrencies"
            :key="index"
            >{{ currency[0] + "-" + currency[1] }}</option
          >
        </select>
      </div>
    </div>
    <div class="result">
      <p v-if="rate">Вы получите: {{ convertedCurrency }}{{ toCurrency }}</p>
      <p v-if="rate">Курс: 1{{ fromCurrency }} = {{ rate }}{{ toCurrency }}</p>
      <button @click="convertCurrencies" class="waves-effect waves-light btn">
        Сконвертировать
      </button>
    </div>
  </div>
</template>

<script>
require("../../node_modules/materialize-css/dist/js/materialize.min.js");
import { required, numeric } from "vuelidate/lib/validators";
import axios from "axios";
export default {
  data() {
    return {
      currencies: {},
      fromCurrency: "",
      toCurrency: "",
      amountFromCurrency: "",
      convertedCurrency: "",
      apiKey: "?api_key=4e99409143063afac3aa5b5d7fe185ba5ac4a844",
      proxy: "https://cors-anywhere.herokuapp.com/",
      rate: null,
      currentCurrency: "",
    };
  },
  async created() {
    await this.getListCurrencies();
    this.checkIP();
  },
  computed: {
    sortedCurrencies() {
      let newarr = Object.entries(this.currencies);
      newarr.sort((a, b) => a[0] - b[0]);
      return newarr.reduce((totalarr, item) => {
        if (item[0] == this.currentCurrency) {
          totalarr.unshift(item);
        } else {
          totalarr.push(item);
        }
        return totalarr;
      }, []);
    },
  },
  methods: {
    getListCurrencies() {
      axios
        .get(
          `${this.proxy}https://api.getgeoapi.com/api/v2/currency/list${this.apiKey}`
        )
        .then((response) => {
          this.currencies = response.data.currencies;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    checkCorrectSubmit() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
    },
    convertCurrencies() {
      if (this.checkCorrectSubmit()) {
        return;
      }
      axios
        .get(
          `${this.proxy}https://api.getgeoapi.com/api/v2/currency/convert${this.apiKey}&from=${this.fromCurrency}&to=${this.toCurrency}&amount=${this.amountFromCurrency}`
        )
        .then((response) => {
          this.convertedCurrency = (
            response.data.amount * response.data.rates[this.toCurrency].rate
          ).toFixed(2);
          this.rate = response.data.rates[this.toCurrency].rate;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    checkIP() {
      axios
        .get(
          `${this.proxy}https://api.getgeoapi.com/api/v2/ip/check${this.apiKey}`
        )
        .then((response) => {
          this.currentCurrency = response.data.currency.code;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  },
  validations: {
    amountFromCurrency: {
      required,
      numeric,
    },
  },
};
</script>

<style lang="scss">
@import url("../../node_modules/materialize-css/dist/css/materialize.min.css");
* {
  box-sizing: border-box;
}
body {
  background: rgb(179, 187, 187);
  background: linear-gradient(
    348deg,
    rgba(179, 187, 187, 1) 10%,
    rgba(239, 240, 227, 0.9920343137254902) 89%
  );
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  scroll-behavior: smooth;
}
.main-page {
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  margin: auto;
  background: white;
  border: 1px solid black;
  padding: 1em;
}
.title {
  margin: auto;
  text-decoration: underline;
  color: #f57c00;
}
.from,
.to {
  width: 100%;
  p {
    font-size: 1.2em;
    color: #4db6ac;
  }
}
.input-field {
  width: 100%;
  select {
    width: 50%;
    margin: auto;
    border: 1px solid black;
  }
  input {
    width: 50%;
    margin: auto;

    background-color: white !important;
    &::placeholder {
      color: black;
    }
  }
}
.from-currency-input {
  width: 50%;
  margin: auto;
}
.result {
  width: 100%;
}
@media screen and (max-width: 768px) {
  .main-page {
    width: 100%;
    min-height: 100vh;
  }
  .input-field {
    width: 100%;
    select {
      width: 100%;
      margin: auto;
    }
    input {
      width: 100%;
      margin: auto;
    }
  }
}
</style>
